*{
    margin: 0px;
    padding: 0px;
}
.container{
    color: white;

}
ul{
    list-style: none;
    margin: 20px auto;
    height:40px;
    width:80%;
    border-radius: 10px;
    background-color: rgb(52, 212, 132);
    box-shadow: 2px 2px 5px rgba(17, 16, 16, 0.9) ;
}
li{
    padding: 5px;
    font-size: 20px;
    font-family: 'Times New Roman', Times, serif;
    font-weight: 600;
    text-shadow:2px 2px 5px rgb(17, 16, 16)  ;
}
a{
    text-decoration: none;
    color: rgb(240, 106, 83);
    font-size: 25px;
    float:right;
    margin-right: 20px;
}
