.App {
  text-align: center;
  height: 100vh;
  width: 100%;
  background-color:rgb(48, 46, 46);
  display: flex;
  justify-content: center;
  align-items: center;

}
.container{
  height: 80vh;
  width:50%;
  box-shadow: 0 5px 9px 0 rgba(0, 0, 0, 0.8), 0 7px 22px 0 rgba(0, 0, 0, 0.8);
  background-color: rgb(165, 156, 25);
  border-radius: 5px;
}
h1{
  margin-top: 20px;
  padding: 10px;
  color: white;
  font-size: 40px;
  font-family:"Adobe Caslon Pro";
  letter-spacing: 4px;
  text-shadow:  2px 2px 8px rgba(17, 16, 16, 0.9);
  border-bottom: 1.5px solid rgb(247, 95, 95);
  border-width: 50%;
}
form{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
input{
  margin-top: 15px;
}
input[type="text"]{
  height: 35px;
  width: 220px;
  border-radius: 5px;
  border: none;
  padding: 0px 25px;
  box-shadow:2px 2px 5px rgba(17, 16, 16, 0.9) ;
  font-size: 15px;
  color: tomato;

}
input::placeholder{
  font-size: 15px;
  color:tomato;
}
input[type="text"]:focus,input[type="submit"]:focus{
  outline: none;
  box-shadow:2px 6px 10px rgba(17, 16, 16, 0.9) ;
}
input[type="submit"]{
  width: 100px;
  height:35px;
  border-radius:5px;
  background-color: tomato;
  border-color: tomato;
  color: white;
  font-size: 15px;
  margin-left: 40px;
}
input[type="submit"]:hover{
  cursor: pointer;
  background-color: tomato;
  transition:0.3s ;
  box-shadow:2px 2px 5px rgba(17, 16, 16, 0.9) ;
}
